
canvas {
    width: 300px;
    height: 300px;
}
.cursorPointer{
    cursor: pointer;
}
.cursorGrab{
    cursor: grab;
    cursor: -webkit-grab;
    cursor: -moz-grab;
}
.cursorGrabbing{
    cursor: grabbing;
    cursor: -webkit-grabbing;
    cursor: -moz-grabbing;
}



